*,
*::before,
*::after {
    box-sizing: border-box;
}
* {
    margin: 0;
}
.gamePoster {
    width: 600px;
    height: 300px;
    position: absolute;
    left: max(calc(600px * 5), 100%); 
    animation-name: scrollleft;
    animation-duration: 30s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}
.gamePoster img {
    width: 100%;
    height: 100%;
}
.slide-container {

    width: 90%;
    max-width: 1536px;
    margin-inline: auto;
    position: relative;
    height: 300px; 
    margin-top: 5rem;
    overflow: hidden;
    mask-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0),
        rgba(0, 0, 0, 1) 20%,
        rgba(0, 0, 0, 1) 80%,
        rgba(0, 0, 0, 0)
    );
}
@keyframes scrollleft {
    to {
        left: -600px; 
    }
}
.img0 {
    animation-delay: calc(100s / 20 * (20 - 1) * -1);
}
